@import 'components/variables';

.section-welcome {
  background-image: url(../images/bg-welcome.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 6rem;
  display: flex;
  align-items: center;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .title {
    text-align: center;
    @media (min-width: 768px) {
      text-align: start;
    }
  }
  .content-reserva {
    background-color: $yellow;
    padding: 1.5rem;
    border-radius: 15px;
    text-align: center;
    position: relative;
    img {
      position: absolute;
      top: 0;
      right: 0;
      transform: translateX(-50%);
      width: 20px;
    }
    a {
      text-decoration: none;
      color: $text-gray;
      transition: all .5s;
      &:hover {
        color: $blue;
      }
    }
  }
}
.section-contacto {
  background-color: #F5F8FD;
  .container {
    background: linear-gradient(to bottom, rgba(150,221,241,1) 0%, rgba(255,255,255,1) 8%, rgba(255,255,255,1) 12%, rgba(255,255,255,1) 100%);
    border-radius: 15px;
    transform: translateY(-4rem);
    .content-contacto {
      padding: 4rem 3rem;
      iframe {
        width: 100%;
        height: 300px;
      }
      .col-izq {
        order: 2;
        @media (min-width: 768px) {
          order: 1;
        }
        .content-map {
          background-color: #353A34;
          padding: 1rem 0;
          p {
            color: $white;
          }
          .fa-map-marker-alt {
            color:$blue;
          }
        }
      }
      .col-der {
        order: 1;
        @media (min-width: 768px) {
          padding: 0 3rem;
          order: 2;
        }
        .text-yellow-contacto {
          font-size: 2rem;
          font-weight: 600;
          color: $yellow;
          text-align: center;
        }
      }
    }
  }
}